const string = `
/*
大家好
今天我来给大家画一个
皮卡丘！
*/
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
/*
首先先画出背景
*/
body{
    background: #ffe600;
    min-height: 100vh;
}
.skin{
    display: block;
    height: 200px;
}
/*
然后画出它的鼻子
*/
.nose{
    border: 10px solid;
    border-color: black transparent blue transparent;
    border-bottom: none;
    width: 10px;
    height: 5px;
    position: relative;
    left: 50%;
    top: 145px;
    margin-left: -10px;
    z-index: 1;
}
@keyframes wave {
    0%{
        transform: rotate(0deg);
    }
    33%{
        transform: rotate(15deg);
    }
    66%{
        transform: rotate(-15deg);
    }
    100%{
        transform: rotate(0deg);
    }
}
.nose:hover{
    transform-origin: 50% 100%;
    animation: wave 300ms infinite linear;
}

.yuan{
    position: absolute;
    background: black;
    width: 20px;
    height: 5px;
    top: -15px;
    left: -10px;
    border-radius: 10px 10px 0 0;
}
/*
然后换出它的眼睛
*/
.eye{
    border: 2px solid black;
    width: 64px;
    height: 64px;
    position: absolute;
    left: 50%;
    top: 100px;
    margin-left: -32px;
    border-radius: 50%;
    background-color: rgb(46,45,46);
}
.eye::before{
    content: '';
    border: 2px solid black;
    background: #fff;
    display: block;
    width: 25px;
    height: 25px;
    position: relative;
    left: 9px;
    top: 1px;
    border-radius: 50%;
}
.eye.left{
    transform: translateX(-130px);
}
.eye.right{
    transform: translateX(130px);
}
/*
然后是嘴巴
*/
.mouth{
    width: 140px;
    height: 200px;
    position: absolute;
    left: 50%;
    top: 170px;
    margin-left: -70px;
}
.mouth .up{
    position: relative;
    top: -15px;
    z-index: 1;
}
.mouth .up .lip{
    border: 3px solid black;
    height: 30px;
    width: 100px;
    border-top-color: transparent;
    position: relative;
    position: absolute;
    left: 50%;
    margin-left: -50px;
    background: #ffe600;
}
.mouth .up .lip.left{
    border-radius: 0 0 0 50px;
    border-left-color: transparent;
    transform: rotate(-15deg) translateX(-53px);
}
.mouth .up .lip.right{
    border-radius: 0 0 50px 0;
    border-right-color: transparent;
    transform: rotate(15deg) translateX(53px);
}
.mouth .up .lip::before{
    content: '';
    display: block;
    width: 7px;
    height: 30px;
    position: absolute;
    bottom:0;
    background: #ffe600;
}
.mouth .up .lip.left::before{
    right: -6px;
}
.mouth .up .lip.right::before{
    left: -6px;
}
.mouth .down{
    height: 160px;
    position: absolute;
    top: 12px;
    width: 200px;
    margin-left: -30px;
    overflow: hidden;
}
.mouth .down .yuan1{
    border: 2px solid black;
    width: 75%;
    height: 1000px;
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-left: -75px;
    border-radius: 75px/300px;
    background: rgb(156,0,12);
    overflow: hidden;
}
.mouth .down .yuan1 .yuan2{
    width: 200px;
    height: 300px;
    position: absolute;
    bottom: -170px;
    left: 50%;
    margin-left: -100px;
    border-radius: 100px;
    background: rgb(255,71,96);
}
/*
最后是圆脸
*/
.face{
    position: absolute;
    left: 50%;
    border: 3px solid black;
    width: 88px;
    height: 88px;
    top: 200px;
    margin-left: -44px;
    z-index: 3;
    border-radius: 50%;
    background: rgb(255,24,0);
}
.face.left{
    transform: translateX(-200px);
}
.face.right{
    transform: translateX(200px);
}
`

export default string